.box-count {
  text-align: center;
  margin: 0px 0px 20px 0px;
}
.box-count .deals-countdown {
  border-radius: 34px;
  background-color: #425A8B;
  display: flex;
  padding: 4px 12px;
  width: 84%;
  margin: auto;
  max-width: 230px;
}
.box-count .deals-countdown .countdown-section {
  display: inline-block;
  text-align: center;
  width: 25%;
  line-height: 12px;
  position: relative;
}
.box-count .deals-countdown .countdown-section::before {
  content: ":";
  position: absolute;
  top: 3px;
  right: -1px;
  font-weight: bold;
  font-size: 16px;
  color: #ffffff;
}
.box-count .deals-countdown .countdown-section span {
  display: block;
  color: #ffffff;
}
.box-count .deals-countdown .countdown-section .countdown-period {
  text-transform: capitalize;
}
.box-count .deals-countdown .countdown-section:last-child::before {
  display: none;
}
.box-count.box-count-square {
  margin-bottom: 5px;
}
.box-count.box-count-square .deals-countdown {
  background-color: #ffffff;
  width: 100%;
  margin: auto;
  text-align: center;
  display: inline-block;
  max-width: 250px;
}
.box-count.box-count-square .deals-countdown .countdown-section {
  display: inline-block;
  text-align: center;
  line-height: 12px;
  position: relative;
  margin: 0px 7px;
  width: auto;
}
.box-count.box-count-square .deals-countdown .countdown-section::before {
  content: ":";
  position: absolute;
  top: 12px;
  right: -9px;
  font-weight: bold;
  font-size: 16px;
  color: #425A8B;
}
.box-count.box-count-square .deals-countdown .countdown-section .countdown-period {
  color: #8C9EC5;
}
.box-count.box-count-square .deals-countdown .countdown-amount {
  background-color: #FD9636;
  border-radius: 4px;
  color: #ffffff;
  padding: 10px 0px;
  width: 42px;
  display: inline-block;
  margin-bottom: 5px;
}
.box-count.hide-period {
  margin-bottom: 0px;
  position: relative;
  bottom: -5px;
  display: inline-block;
  vertical-align: middle;
}
.box-count.hide-period .deals-countdown {
  padding: 4px 2px;
}
.box-count.hide-period .countdown-section .countdown-period {
  display: none;
}

