.list-brands {
  display: flex;
  width: 100%;
  border: 1px solid #D5DFE4;
  padding: 16px;
  border-radius: 4px;
  margin: 10px 0px 0px 0px;
}
.list-brands a {
  display: inline-block;
  width: 100%;
  text-align: center;
  vertical-align: middle;
  line-height: 100%;
}
.list-brands a img {
  opacity: 0.3;
  max-height: 15px;
}
.list-brands a:hover img {
  opacity: 1;
}

.head-main {
  display: inline-block;
  width: 100%;
  padding: 0px 0px 20px 0px;
  border-bottom: 1px solid #D5DFE4;
  margin-bottom: 30px;
  position: relative;
}

.nav-tabs {
  border-bottom: 0px;
  padding-top: 40px;
  display: inline-block;
  text-align: right;
  width: 100%;
  vertical-align: bottom;
}
.nav-tabs li {
  display: inline-block;
  padding: 0px 10px 0px 10px;
}
.nav-tabs li a {
  display: inline-block;
  width: 100%;
  padding: 0px 10px;
  font-size: 16px;
  line-height: 24px;
  color: #8C9EC5;
}
.nav-tabs li a:hover {
  color: #425A8B;
  font-weight: 400;
}
.nav-tabs li a.active {
  color: #425A8B;
  font-weight: bold;
}
.nav-tabs li:last-child {
  padding-right: 0px;
}
.nav-tabs li:last-child a {
  padding-right: 0px;
}

.list-links-disc {
  display: inline-block;
  width: 100%;
  padding: 15px 0px 10px 0px;
}
.list-links-disc li {
  margin: 0px 0px 6px 0px;
  list-style: none;
  transition: 0.2s;
}
.list-links-disc li:hover {
  padding-left: 2px;
  transition: 0.2s;
}
.list-links-disc li a {
  display: block;
  padding: 0px 0px 0px 10px;
  background: url(../imgs/template/icons/arrow-small.svg) no-repeat left center;
  color: #6B83B6;
}
.list-links-disc li a:hover {
  color: #0BA9ED;
  background: url(../imgs/template/icons/arrow-small-hover.svg) no-repeat left center;
}

.image-gallery {
  display: table;
  table-layout: fixed;
  margin: 0px -10px 20px -10px;
  width: calc(100% + 20px);
}
.image-gallery .image-big {
  float: left;
  width: 62.5%;
  padding: 0px 10px;
}
.image-gallery .image-small {
  float: left;
  width: 37.5%;
  padding: 0px 10px;
}

