.banner-ads {
  background: url(../imgs/page/homepage3/bg-ads.png) no-repeat 0px 0px;
  background-size: cover;
  padding: 50px 10px;
  position: relative;
}
.banner-ads::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0px;
  height: 100%;
  width: 400px;
  background: url(../imgs/page/homepage3/bg-left-ads.png) no-repeat bottom right;
  background-size: contain;
}
.banner-ads::after {
